<!-- Lynn -->
<script>
import tableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";

export default {
  name: "signedDocument-component",
  components: { tableComponent, Pagination1 },
  data() {
    return {
      tableColumns: [
        { id: "index", label: "項次", width: "70" },
        { id: "proj", label: "工程編號 (工程名稱)", width: "280" },
        { id: "period", label: "建立日期 (開始 ~ 結束日期)", width: "240" },
        { id: "region", label: "工作區域", width: "240" },
        {
          id: "contract_info",
          label: "主承包商編號&名稱 / 下包廠商數",
          width: "240",
        },
        { id: "act_Desc", label: "簽核節點", width: "175" },
      ],
      tableData: [], // 表格資料 (All)
      tableList: [], // 表格資料 (單頁)
    };
  },
  methods: {
    async getSignOffDoc() {
      const res = await this.$store.dispatch("getSignOffDoc");
      const data = res.data.data;
      this.tableData = JSON.parse(JSON.stringify(data));

      // 將取得的資料，做顯示前加工處理
      this.tableData.forEach((e, index) => {
        e.index = index + 1;
        e.proj = `${e.project_No} (${e.project_Name})`;
        e.period = `${e.create_Date} (${e.begin_Date} ~ ${e.end_Date})`;
        e.region = `${e.ld2} / ${e.ld1} / ${e.ld4} / ${e.ld3} / ${e.floor}`;
        e.contract_info = `${e.s_Id} ${e.s_name} / ${e.s_Count}`;
      });
      this.tableList = this.tableData.slice(0, 10);
    },

    // 開新畫面 token 會被清除，故需事先取 API 資料
    async intoViewSignOffDetails(id) {
      let data = [];
      try {
        const getProjDetailRes = await this.$store.dispatch(
          "getProjDetail",
          id
        );
        const getSignOffHistoryRes = await this.$store.dispatch(
          "getSignOffHistory",
          id
        );
        // [工程單及承包商資料、簽核歷程資料]
        data = [getProjDetailRes.data.data, getSignOffHistoryRes.data.data];
      } catch (err) {
        console.log(err);
      }

      // 以 query 夾帶 JSON 字串傳送資料
      const { href } = this.$router.resolve({
        path: "/viewSignOffDetails",
        query: {
          data: JSON.stringify(data),
        },
      });
      window.open(href, "_blank");
    },

    // 依據頁數，更新對應的 tableList 資料
    changePage(page) {
      this.$store.state.currentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // 打 API 取得資料，呈現表格第一頁
    this.getSignOffDoc();
    this.$store.state.currentPage = 1;
  },
};
</script>

<template src="./signedDocument.html"></template>
